import React from "react"
import { Translation } from "react-i18next"
import i18n from "../lib/i18n"

import HeroHalf from "./heros/hero-half"
import FeaturesAC from "./features/features-ac"
import InfinityProducts from "./infinity-products"
import Team from "./team"
import Testimonials from "./testimonials"
import FaqAC from "./faq/ac-faq"
import SpecialsCTA from "./cta/specials-cta"

const AC = ({ data, language }) => {
  const hero = data.wpHero.heroFields
  return (
    <Translation>
      {t => (
        <>
          <HeroHalf
            headline={`${hero[i18n.language].headline}`}
            smallcta={``}
            subheadline={`${hero[i18n.language].subheadline}`}
            body={`${hero[i18n.language].body}`}
            image={`${hero[i18n.language].image.sourceUrl}`}
            button2Text={`${hero[i18n.language].buttontext}`}
            button2Link={
              hero[i18n.language].buttonlink !== null
                ? `${hero[i18n.language].buttonlink}`
                : `#getInTouch`
            }
          />
          <FeaturesAC />
          <SpecialsCTA />
          <InfinityProducts />
          <FaqAC />
          <Team />
          <Testimonials />
        </>
      )}
    </Translation>
  )
}
